.external-wrapper {
  overflow: hidden;
  background: #edeef0;
}

.app-wrapper {
  max-width: 970px;
  margin: 0 auto;
  display: grid;
  grid-template-rows: auto 1fr auto;
  min-height: 100vh;
  grid-template-columns: 2fr 12fr;
  grid-template-areas:
          'h h'
          'n a'
          'f f';
}

.wrapper_content {
  margin-top: 24px;
  grid-area: a;
  margin-left: 10px;
}